import { toast } from "react-toastify";
import { REDUCER } from "../auth/reducer";
import ACTIONS from "../auth/actions";
import { DataObjectOutlined } from "@mui/icons-material";

export const resolveResponse = (
  resp,
  successCb = Function.prototype,
  successMsg = "",
  errorCb = Function.prototype,
  noToast
) => {
  const { data } = resp;
  if (resp?.status === 200 || resp?.status === 201 ||resp?.status === 202 || resp?.status === 204) {
    const { message } = data;
    !noToast && toast.success(successMsg || message);
    if (successCb) successCb(data);
  } else if (resp?.statusText !== "OK") {
    toast.error("Something might went wrong!");
    if (errorCb) errorCb();
  }
};

const T_USER = "t_user";
const T_TOKEN = "t_token";

const storeToken = localStorage.getItem(T_TOKEN);
export const storeUser = localStorage.getItem(T_USER);

export const resetAuth = (dispatch, state) => {
  if (
    !state.token &&
    !state.user &&
    !!storeToken &&
    storeUser !== "undefined" &&
    !!storeUser
  ) {
    dispatch(
      REDUCER[ACTIONS.SET_USER]({
        token: storeToken,
        data: JSON?.parse(storeUser) || {},
      })
    );
  }
};

export const setStorage = (data) => {
  localStorage.setItem(T_USER,JSON.stringify(data));
};

export const setToken = (token) => {
  localStorage.setItem(T_TOKEN, token);
};

export const removeStorage = () => {
  localStorage.removeItem(T_USER);
  localStorage.removeItem(T_TOKEN);
};
